.list {
    display: flex;
    flex-direction: column;
    gap: 15px;
    list-style: none;
}

.item {
    display: flex;
    gap: 15px;
    padding: 15px;
}

.input {
    display: inline-block;
    width: 100%;
    font: inherit;
    font-size: 20px;
    outline: none;
    padding-left: 4px;
    padding-right: 4px;
}

.button {
    padding: 8px 8px;
    border-radius: 10px;
    background-color: #43b53f;
    transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
    text-align: center;
    display: inline-block;
    color: #fff;
    border: 0;
    text-decoration: none;
    cursor: pointer;
    font-family: inherit;
    font-size: 18px;
    line-height: 24px;
    font-style: normal;
    font-weight: 500;
    min-width: 120px;
    box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
        0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
}

.button:hover,
.button:focus {
    background-color: #309f43;
    box-shadow: 0px 5px 9px rgba(0, 0, 0, 0.3);
}